import React, { useState } from 'react';
import { Router } from '@reach/router'
import { Branch, Site, Span, Button } from '@horizin/design-system'
import { BoxLoginButton } from '@kames/3box-components/dist'
import { SiteHeader, SiteFooter } from '@horizin/design-system/dist/templates/Site'
import { Branding, Footer } from "../components";
import {
    Account
  } from "../views/Site";
  import {
    EthereumEnableMetaMask
  } from "../components/Signatures";

  
const MobileRoutes = ({ styled, ...props }) => {
  const [ address, setAddress ] = useState()
  const act = async props => {
    const accounts = await window.ethereum.enable()
    setAddress(accounts[0])
    console.log(accounts)
  }

  return (
    <Site>
      <SiteHeader>
        <BoxLoginButton />
        <EthereumEnableMetaMask />
        {/* <Branding /> */}
        <br/><Span>metamask: {window.ethereum.isMetaMask ? `${true}` : `${false}`}</Span><br/>
        <br/><Span>address: {window.ethereum.selectedAddress ? 'true' : 'false'} </Span><br/>
        <Button onClick={act}>enable</Button>
        {address}
        <Span>{window.web3.currentProvider.chainId}</Span>
        <br/>
        {
          window.web3.eth && window.web3.eth.accounts[0]
        }
      </SiteHeader>
        {/* <Branch data={window.web3}/> */}
        <Router primary={false}>
          <Account path='/*' />
        </Router>
      <SiteFooter>
        {/* <Footer /> */}
      </SiteFooter>
    </Site>
  )
}
export default MobileRoutes